import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { ContactDemand } from "src/app/models/ContactDemand";
import { ContactDemandService } from "src/app/services/contact-demand.service";

@Component({
  selector: "app-contact-details-admin",
  templateUrl: "./contact-details-admin.component.html",
  styleUrls: ["./contact-details-admin.component.css"]
})
export class ContactDetailsAdminComponent implements OnInit {
  isLoading = true;
  form: FormGroup;
  contactDemand: ContactDemand;
  constructor(
    private httpContactDemand: ContactDemandService,
    private router: Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.activateForm();
    this.route.paramMap.subscribe((paramMap: ParamMap) => {
      if (paramMap.has("contactId")) {
        const contactDemandId = +paramMap.get("contactId");
        this.getContactDemandId(contactDemandId);
      }
    });
  }

  getContactDemandId(contactDemandId: number) {
    this.httpContactDemand.getContactDemandById(contactDemandId).subscribe(
      data => {
        this.contactDemand = data;
      },
      error => console.log(error),
      () => {
        this.isLoading = false;
      }
    );
  }

  saveContactDemand() {
    if (this.form.invalid) {
      return;
    }
    this.checkFormValues();
    this.httpContactDemand.updateContactDemand(this.contactDemand).subscribe(
      data => {
      },
      error => console.log(error),
      () => {
        this.router.navigate(["/admin/contacts"]);
      }
    );
  }

  checkFormValues() {
    this.contactDemand.answer = this.form.value.answer;
  }

  activateForm() {
    this.form = new FormGroup({
      answer: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      })
    });
  }
}
