import { ServiceOfferedService } from "./../../../../services/service-offered.service";
import { Component, OnInit } from "@angular/core";
import { OfferedService } from "src/app/models/OfferedService";
import { Router } from "@angular/router";

@Component({
  selector: "app-offered-services-list",
  templateUrl: "./offered-services-list.component.html",
  styleUrls: ["./offered-services-list.component.css"]
})
export class OfferedServicesListComponent implements OnInit {
  isLoading = true;
  listOfferedServices: OfferedService[] = [];

  constructor(
    private httpOfferedServices: ServiceOfferedService,
    private router: Router
  ) {}

  ngOnInit() {
    this.getOfferedServices();
  }

  getOfferedServices() {
    this.httpOfferedServices.getOfferedServices().subscribe(
      data => {
        this.listOfferedServices = data;
      },
      error => console.log(error),
      () => {
        this.isLoading = false;
      }
    );
  }

  onDelete(offService: OfferedService) {
    if (
      confirm(
        "tes-vous sr de vouloir supprimer le service " +
          offService.serviceName +
          "?"
      )
    ) {
      this.isLoading = true;
      this.httpOfferedServices.deleteOfferedService(offService.id).subscribe(
        data => {
          this.getOfferedServices();
        },
        error => {
          console.log(error);
          this.isLoading = false;
        }
      );
    }
  }
}
