import { ContactDemandService } from './../../services/contact-demand.service';
import { ContactDemand } from './../../models/ContactDemand';
import { Component, OnInit } from '@angular/core';
import { CompanyService } from 'src/app/services/company.service';
import { Company } from 'src/app/models/Company';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Client } from 'src/app/models/Client';
import * as EmailValidator from 'email-validator';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  contactPreferences: string[] = ["Tlphone", "Courriel"];
  company: Company;
  isLoading = true;
  form: FormGroup;
  uploadCompleted = false;
  uploadError = false;

  constructor(private httpCompany: CompanyService, private httpContactDemand: ContactDemandService) { }

  ngOnInit() {
    this.getCompany();
    this.activateFormVerify();
  }

  sendContact() {
    this.isLoading = true;
    const contactDemand = this.checkValuesOfContactForm();
    if (this.form.invalid) {
      this.isLoading = false;
      return;
    }
    if (contactDemand != null) {
      this.httpContactDemand.postContactDemand(contactDemand).subscribe(data => {
        if (data.message === "ok") {
        this.uploadCompleted = true;
      }
      }, error => console.log(error), ()=> {
        this.isLoading = false;
        if (this.uploadCompleted) {
          this.uploadError = false;
          const elementToScroll = document.querySelector("#sendSuccess");
          if (elementToScroll) elementToScroll.scrollIntoView({behavior: "smooth", block: "start", inline: "nearest"});
        } else {
          this.uploadError = true;
          const elementToScroll = document.querySelector("#sendError");
          if (elementToScroll) elementToScroll.scrollIntoView({behavior: "smooth", block: "start", inline: "nearest"});
        }
      });


    }

  }

  checkValuesOfContactForm(): ContactDemand {
    // TODO ajouter des tests de vrification des donnes
    const client = new Client();
    client.firstName = this.form.value.fName;
    client.lastName = this.form.value.lName;
    if (!EmailValidator.validate(this.form.value.email)) {
      this.form.controls["email"].setErrors({"incorrect":true});
      return null;
    }
    client.email = this.form.value.email;
    client.phoneNumber = this.form.value.phone;
    client.phoneNumber = client.phoneNumber.split('-').join('');
    if (client.phoneNumber.match(/[a-z]/i)) {
      this.form.controls['phone'].setErrors({"incorrect":true});
      return null;
    }

    const contactDemand = new ContactDemand();
    contactDemand.client = client;
    contactDemand.description = this.form.value.content;
    contactDemand.preferedContactMethod = this.form.value.contactPreference;
    return contactDemand;
  }

  activateFormVerify() {
    this.form = new FormGroup({
      fName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      lName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      email: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(5)]
      }),
      phone: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(10), Validators.maxLength(12)]
      }),
      content: new FormControl(null, { validators: [Validators.required] }),
      contactPreference: new FormControl(this.contactPreferences[0], { validators: [Validators.required] }),
    });
  }

  getCompany() {
    this.httpCompany.getCompany().subscribe(data => {
      this.company = data;
    }, error => { console.log(error); }, () => { this.isLoading = false; });
  }
}
