import { environment } from "./../../../../../environments/environment.prod";
import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { Picture } from "src/app/models/Picture";
import { Project } from "src/app/models/Project";
import { ProjectService } from "src/app/services/project.service";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { mimeType } from "src/app/models/file-type.validator";

@Component({
  selector: "app-project-detail-admin",
  templateUrl: "./project-details-admin.component.html",
  styleUrls: ["./project-details-admin.component.css"]
})
export class ProjectDetailsAdminComponent implements OnInit {
  isLoading = true;
  form: FormGroup;
  imagePreview: string;
  pictures: Picture[] = [];
  imgError = false;
  reader: FileReader;
  project: Project;
  files: File[];

  constructor(
    private httpProjectService: ProjectService,
    private route: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {
    this.route.paramMap.subscribe((paramMap: ParamMap) => {
      if (paramMap.has("projectId")) {
        const projectId = +paramMap.get("projectId");
        this.getProjectById(projectId);
      }
    });
    this.activateForm();
  }

  getProjectById(projectId: number) {
    this.httpProjectService.getProjectById(projectId).subscribe(
      data => {
        this.project = data;
        this.project.listePictures.forEach(element => {
          element.lienPhoto = environment.photoBaseApi + element.lienPhoto;
        });
      },
      error => console.log(error),
      () => {
        this.setDefaultValues();
        this.isLoading = false;
      }
    );
  }

  setDefaultValues() {
    this.files = [];
    this.form.setValue({
      projectName: this.project.projectName,
      description: this.project.description,
      image: "empty"
    });
  }

   onImagePicked(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file });
    this.reader = new FileReader();
    this.reader.onload = () => {
      if (this.form.get("image").valid) {
        this.imgError = false;
        this.imagePreview = this.reader.result as string;
        this.files.push(file);
        this.addPicture(file, this.imagePreview);
      } else {
        this.imgError = true;
        this.form.get("image").setValue("empty");
        return;
      }
    };
    this.reader.readAsDataURL(file);
  }

  addPicture(file: File, imagePreview: string) {
    const pic = new Picture();
    pic.lienPhoto = imagePreview;
    pic.nomPhoto = this.createUniqueName(file);
    pic.descriptionPhoto = "";
    this.project.listePictures.push(pic);
  }

  deleteProject() {
    if (
      confirm(
        "tes-vous sr de vouloir supprimer le projet " +
          this.project.projectName +
          "?"
      )
    ) {
      this.httpProjectService.deleteProject(this.project.id).subscribe(
        data => {
        },
        error => {
          console.log(error);
          this.isLoading = false;
        },
        () => {
          this.isLoading = true;
          this.router.navigate(["admin/projects"]);
        }
      );
    }
  }

  createUniqueName(file: File) {
    const fileNameArray = file.name;
    return fileNameArray;
  }

  activateForm() {
    this.form = new FormGroup({
      projectName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      description: new FormControl(null),
      image: new FormControl(null, {
        validators: [Validators.required],
        asyncValidators: [mimeType]
      })
    });
  }

  saveProject() {
    if (this.form.invalid) {
      if (this.form.get("image").invalid) {
        this.imgError = true;
      }
      return;
    }
    if (this.project.listePictures.length === 0) {
      this.imgError = true;
      return;
    }
    this.imgError = false;
    this.isLoading = true;
    if (this.checkValues(this.project)) {
      console.log(this.project);
      this.httpProjectService.updateProject(this.project, this.files).subscribe(
        data => {
        },
        error => {
          console.log(error);
          this.isLoading = false;
        },
        () => {
          this.isLoading = false;
          this.router.navigate(["/admin/projects"]);
        }
      );
    }
  }

  deletePhoto(pict: Picture) {
    this.project.listePictures = this.project.listePictures.filter(
      resultArray => {
        return resultArray.id !== pict.id;
      }
    );
  }

  checkValues(project: Project): boolean {
    const projectName = this.form.value.projectName;
    const projectDescription = this.form.value.description;
    this.project.listePictures.forEach(element => {
      if (element.descriptionPhoto.length <= 0) {
        element.descriptionPhoto = "";
      }
    });

    // TODO check name and desc
    project.projectName = projectName;
    project.description = projectDescription;

    return true;
  }
}
