import { Component, OnInit } from "@angular/core";
import { Employee } from "src/app/models/Employee";
import { TeamService } from "src/app/services/team.service";
import { environment } from 'src/environments/environment';

@Component({
  selector: "app-team-admin",
  templateUrl: "./team-list-admin.component.html",
  styleUrls: ["./team-list-admin.component.css"]
})
export class TeamListAdminComponent implements OnInit {
  listeEmployees: Employee[] = [];
  isLoading = true;

  constructor(private httpTeam: TeamService) {}

  ngOnInit() {
    this.getTeam();
  }

  getTeam() {
    this.httpTeam.getTeam().subscribe(
      data => {
        this.listeEmployees = data;
        this.listeEmployees.forEach(emp => {
          emp.pictureLink = environment.photoBaseApi + emp.pictureLink;
        });
      },
      error => {
        console.log(error);
      },
      () => {
        this.isLoading = false;
      }
    );
  }

  onDelete(employee: Employee) {
    if (
      confirm(
        "tes-vous sr de vouloir supprimer l'employ " +
          employee.firstName +
          " " +
          employee.lastName +
          "?"
      )
    ) {
      this.isLoading = true;
      this.httpTeam.deleteEmployee(employee.id).subscribe(
        data => {
          this.getTeam();
        },
        error => {
          console.log(error);
          this.isLoading = false;
        }
      );
    }
  }
}
