import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { Employee } from "../models/Employee";
import { environment } from "src/environments/environment";

@Injectable({
  providedIn: "root"
})
export class TeamService {
  private baseApi = environment.baseApi;

  constructor(private httpClient: HttpClient) {}

  getTeam(): Observable<Employee[]> {
    return this.httpClient.get<Employee[]>(this.baseApi + "team");
  }

  getEmployee(id: number): Observable<Employee> {
    return this.httpClient.get<Employee>(this.baseApi + "team/" + id);
  }

  updateEmployee(employee: Employee, image: File): Observable<Employee> {
    const formData = new FormData();
    formData.append("file", image, image.name);
    formData.append("employee", JSON.stringify(employee));
    return this.httpClient.put<any>(
      this.baseApi + "team/" + employee.id,
      formData
    );
  }

  saveEmployee(employee: Employee, image: File): Observable<Employee> {
    const formData = new FormData();
    employee.id = 0;
    employee.pictureLink = "";
    formData.append("file", image, image.name);
    formData.append("employee", JSON.stringify(employee));
    return this.httpClient.post<any>(this.baseApi + "team/", formData);
  }

  deleteEmployee(employeeId: number): Observable<Employee> {
    return this.httpClient.delete<Employee>(
      this.baseApi + "team/" + employeeId
    );
  }
}
