import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { Project } from "../models/Project";
import { environment } from "src/environments/environment";
import { Picture } from "../models/Picture";

@Injectable({
  providedIn: "root"
})
export class ProjectService {
  private baseApi = environment.baseApi;

  constructor(private httpClient: HttpClient) {}

  getProjects(): Observable<Project[]> {
    return this.httpClient.get<Project[]>(this.baseApi + "projects");
  }

  getProjectById(id: number): Observable<Project> {
    return this.httpClient.get<Project>(this.baseApi + "projects/" + id);
  }

  saveProject(project: Project, picturesList: File[]): Observable<any> {
    const formData = new FormData();
    Array.from(picturesList).map((file, index) => {
      return formData.append("file" + index, file, file.name);
    });
    formData.append("project", JSON.stringify(project));
    return this.httpClient.post<any>(this.baseApi + "projects", formData);
  }

  updateProject(project: Project, picturesList: File[]): Observable<any> {
    const formData = new FormData();
    if (picturesList.length > 0) {
      Array.from(picturesList).map((file, index) => {
        return formData.append("file" + index, file, file.name);
      });
    }
    formData.append("project", JSON.stringify(project));
    console.log(formData);
    return this.httpClient.put<any>(
      this.baseApi + "projects/" + project.id,
      formData
    );
  }

  deletePhotoById(idPhoto: number): Observable<Picture> {
    return this.httpClient.delete<Picture>(
      this.baseApi + "pictures/" + idPhoto
    );
  }

  deleteProject(projectId: number): Observable<Project> {
    return this.httpClient.delete<Project>(
      this.baseApi + "projects/" + projectId
    );
  }
}
