import { Component, OnInit } from "@angular/core";
import { Employee } from "src/app/models/Employee";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { TeamService } from "src/app/services/team.service";
import { Router } from "@angular/router";
import { mimeType } from "src/app/models/file-type.validator";

@Component({
  selector: "app-add-employee",
  templateUrl: "./add-employee.component.html",
  styleUrls: ["./add-employee.component.css"]
})
export class AddEmployeeComponent implements OnInit {
  isLoading = true;
  employee: Employee;
  imgError = false;
  form: FormGroup;
  reader: FileReader;
  imagePreview: string;
  image: File;

  constructor(private httpTeamService: TeamService, private route: Router) {}

  ngOnInit() {
    this.activateFormVerify();
    this.isLoading = false;
  }

  onImagePicked(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file });
    this.reader = new FileReader();
    this.reader.onload = () => {
      if (this.form.get("image").valid) {
        this.imgError = false;
        this.imagePreview = this.reader.result as string;
        this.image = file;
      } else {
        this.imgError = true;
        return;
      }
    };
    try {
      this.reader.readAsDataURL(file);
    } catch (err) {
      console.log(err);
    }
  }

  activateFormVerify() {
    this.form = new FormGroup({
      firstName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      lastName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      role: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(5)]
      }),
      description: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(10)]
      }),
      image: new FormControl(null, {
        validators: [Validators.required],
        asyncValidators: [mimeType]
      })
    });
  }

  saveEmployee() {
    if (this.form.invalid) {
      if (this.form.get("image").invalid) {
        this.imgError = true;
      }
      return;
    }
    this.checkFormInfos();
    console.log(this.employee);
    this.isLoading = true;
    this.httpTeamService.saveEmployee(this.employee, this.image).subscribe(
      data => {
      },
      error => {
        console.log(error);
        this.isLoading = false;
      },
      () => {
        this.isLoading = false;
        this.route.navigate(["admin/team"]);
      }
    );
  }

  checkFormInfos() {
    this.employee = new Employee();
    this.employee.firstName = this.form.value.firstName;
    this.employee.lastName = this.form.value.lastName;
    this.employee.role = this.form.value.role;
    this.employee.description = this.form.value.description;
    this.employee.id = null;
    this.employee.pictureLink = null;
  }
}
