import { WindowScrolling } from "./../../models/WindowScrolling";
import { Component, OnInit } from "@angular/core";
import { Company } from "src/app/models/Company";
import { CompanyService } from "src/app/services/company.service";

@Component({
  selector: "app-header",
  templateUrl: "./header.component.html",
  styleUrls: ["./header.component.css"]
})
export class HeaderComponent implements OnInit {
  company: Company;
  isLoading = true;

  constructor(
    private httpCompany: CompanyService,
    private windowScrolling: WindowScrolling
  ) {}

  ngOnInit() {
    this.getContact();
  }

  getContact() {
    this.httpCompany.getCompany().subscribe(
      data => {
        this.company = data;
      },
      error => {
        console.log(error);
      },
      () => {
        this.isLoading = false;
      }
    );
  }

  setClickListener(event: Event) {
    const inputMenu = event.target as HTMLInputElement;
    if (inputMenu.checked) {
      this.disableScrolling();
      document.querySelector("#menuBtn").classList.remove("fa-bars");
      document.querySelector("#menuBtn").classList.add("fa-times");
    } else {
      this.enableScrolling();
      document.querySelector("#menuBtn").classList.add("fa-bars");
      document.querySelector("#menuBtn").classList.remove("fa-times");
    }
  }

  enableScrolling() {
    this.windowScrolling.disable();
    this.windowScrolling.enable();
    const checkButton = document.querySelector("#check") as HTMLInputElement;
    checkButton.checked = false;
    document.querySelector("#menuBtn").classList.add("fa-bars");
    document.querySelector("#menuBtn").classList.remove("fa-times");
  }


  disableScrolling() {
    this.windowScrolling.disable();
  }
}
