import { QuoteService } from "./../../services/quote.service";
import { ServiceOfferedService } from "src/app/services/service-offered.service";
import { Component, OnInit, Input } from "@angular/core";
import { OfferedService } from "../../models/OfferedService";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import * as $ from "jquery";
import { Quote } from "../../models/Quote";
import { Client } from "src/app/models/Client";
import { Files } from "src/app/models/Files";
import { mimeType } from "../../models/file-type.validator";
import * as EmailValidator from 'email-validator';

@Component({
  selector: "app-quoting",
  templateUrl: "./quoting.component.html",
  styleUrls: ["./quoting.component.css"]
})
export class QuotingComponent implements OnInit {
  contactPreferences: string[] = ["Tlphone", "Courriel"];
  listeServices: OfferedService[] = [];
  serviceAskedFor: OfferedService;
  form: FormGroup;
  serviceNotClicked = true;
  imagesPreviews: string[] = [];
  images: File[] = [];
  imagePreview: string;
  imgError = false;
  reader: FileReader;
  options = ["Tlphone", "Courriel"];
  isLoading = true;
  quote = new Quote();
  uploadCompleted = false;
  uploadError = false;

  constructor(
    private httpServicesOffered: ServiceOfferedService,
    private httpQuotes: QuoteService
  ) { }

  ngOnInit() {
    this.getServicesOffered();
    this.activateFormVerify();
  }

  activateFormVerify() {
    this.form = new FormGroup({
      fName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      lName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      email: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(5)]
      }),
      phone: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(10), Validators.maxLength(12)]
      }),
      content: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(10)]
      }),
      contactPreference: new FormControl(this.contactPreferences[0], {
        validators: [Validators.required]
      }),
      image: new FormControl("empty", {
        asyncValidators: [mimeType]
      })
    });
  }

  onSavePost() {
    if (this.form.invalid) {
      return;
    }
    if (this.checkValues()) {
      this.isLoading = true;
      this.httpQuotes.postQuote(this.quote, this.images).subscribe(
        data => {
          if (data.message = "SUCCESS") {
            this.uploadCompleted = true;
          }
        },
        error => console.log(error),
        () => {
          if (this.uploadCompleted) {
            this.uploadError = false;
            const elementToScroll = document.querySelector("#sendSuccess");
            if (elementToScroll) elementToScroll.scrollIntoView({ behavior: "smooth", block: "start", inline: "nearest" });
          } else {
            this.uploadError = true;
            const elementToScroll = document.querySelector("#sendError");
            if (elementToScroll) elementToScroll.scrollIntoView({ behavior: "smooth", block: "start", inline: "nearest" });
          }

        }
      );
    }
  }

  checkValues(): boolean {
    // TODO: FAIRE UNE LOGIQUE POUR TESTER LES VALEURS
    const client = new Client();
    client.firstName = this.form.value.fName;
    client.lastName = this.form.value.lName;

    if (!EmailValidator.validate(this.form.value.email)) {
      this.form.controls["email"].setErrors({ "incorrect": true });
      return false;
    }
    client.email = this.form.value.email;
    client.phoneNumber = this.form.value.phone;
    client.phoneNumber = client.phoneNumber.split('-').join('');
    if (client.phoneNumber.match(/[a-z]/i)) {
      this.form.controls['phone'].setErrors({ "incorrect": true });
      return false;
    }
    this.form.controls['phone'].setErrors({ "incorrect": false });;
    this.quote.client = client;
    this.quote.description = this.form.value.content;
    this.quote.preferedContactMethod = this.form.value.contactPreference;
    this.quote.offeredService = this.serviceAskedFor;
    return true;
  }

  onImagePicked(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file });
    this.reader = new FileReader();
      this.reader.onload = () => { 
        if (this.form.get("image").valid) {
        this.imgError = false;
        this.imagePreview = this.reader.result as string;
        this.imagesPreviews.push(this.imagePreview);
        this.images.push(file);
        this.addFile(file, this.imagePreview);
      }
      else {
        this.imgError = true;
        this.form.get("image").setValue("empty");
      }
    };
    this.reader.readAsDataURL(file);
  }


  addFile(file: File, imagePreview: string) {
    const fileToAdd = new Files();
    fileToAdd.fileLink = imagePreview;
    fileToAdd.fileName = file.name;
    this.quote.fileLinks.push(fileToAdd);
  }

  getServicesOffered() {
    this.httpServicesOffered.getOfferedServices().subscribe(
      data => {
        this.listeServices = data;
      },
      error => {
        console.log(error);
      },
      () => {
        this.isLoading = false;
      }
    );
  }

  addServiceToQuote(service: OfferedService) {
    $("#quoteSelector").hide("slow");
    this.serviceNotClicked = false;
    this.serviceAskedFor = service;
  }
}
