import { QuoteService } from 'src/app/services/quote.service';
import { Component, OnInit } from '@angular/core';
import { Quote } from 'src/app/models/Quote';

@Component({
  selector: 'app-quotes-list-admin',
  templateUrl: './quotes-list-admin.component.html',
  styleUrls: ['./quotes-list-admin.component.css']
})
export class QuotesListAdminComponent implements OnInit {
  isLoading = true;
  listQuotes: Quote[] = [];
  selectOption = ["incomplete", "complete"];
  selectedOption = this.selectOption[0]; 
  constructor(
    private httpQuote: QuoteService
  ) {}

  ngOnInit() {
    this.getQuotes();
  }

  completeQuote(qte: Quote) {
    qte.completed = 'TRUE';
    this.isLoading = true;
    this.httpQuote.markQuoteCompleted(qte).subscribe(data => {
    },error => {
      console.log(error);
      this.isLoading = false;
    }, ()=> {
      this.getQuotes();
      this.isLoading = false;
    });
  }

  selectQuotesOnChange(value:string) {
    if (value === "incomplete") {
      this.isLoading = true;
      this.selectedOption = this.selectOption[0]; 
      this.getQuotes();
    }

    if (value === "complete") {
      this.isLoading = true;
      this.selectedOption = this.selectOption[1]; 
      this.getQuotesCompleted();
    }
  }

  getQuotes() {
    this.httpQuote.getQuotes().subscribe(
      data => {
        this.listQuotes = data;
      },
      error => console.log(error),
      () => {
        this.isLoading = false;
      }
    );
  }

  getQuotesCompleted() {
    this.httpQuote.getQuotesCompleted().subscribe(
      data => {
        this.listQuotes = data;
      },
      error => console.log(error),
      () => {
        this.isLoading = false;
      }
    );
  }

}
