import { environment } from './../../../environments/environment.prod';
import { TeamService } from './../../services/team.service';
import { Component, OnInit } from '@angular/core';
import { Employee } from 'src/app/models/Employee';

@Component({
  selector: 'app-team',
  templateUrl: './team.component.html',
  styleUrls: ['./team.component.css']
})
export class TeamComponent implements OnInit {
  listeEmployees: Employee[] = [];
  isLoading = true;

  constructor(private httpTeam: TeamService) { }

  ngOnInit() {
    this.getTeam();
  }

  getTeam() {
    this.httpTeam.getTeam().subscribe(data => {
      this.listeEmployees = data;
      this.listeEmployees.forEach(emp => {
        emp.pictureLink = environment.photoBaseApi + emp.pictureLink;
      });
    }, error => { console.log(error); }, () => { this.isLoading = false; });
  }

}
