import { environment } from "./../../environments/environment";
import { Testimony } from "./../models/Testimony";
import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root"
})
export class TestimonyService {
  private baseApi = environment.baseApi;

  constructor(private httpClient: HttpClient) {}

  getTestimonies(): Observable<Testimony[]> {
    return this.httpClient.get<Testimony[]>(this.baseApi + "testimonies");
  }

  getTestimony(id: number): Observable<Testimony> {
    return this.httpClient.get<Testimony>(this.baseApi + "testimonies/" + id);
  }

  deleteTestimony(id: number): Observable<Testimony> {
    return this.httpClient.delete<Testimony>(
      this.baseApi + "testimonies/" + id
    );
  }

  updateTestimony(testimony: Testimony, image: File): Observable<Testimony> {
    const formData = new FormData();
    formData.append("file", image, image.name);
    formData.append("testimony", JSON.stringify(testimony));
    return this.httpClient.put<any>(
      this.baseApi + "testimonies/" + testimony.id,
      formData
    );
  }

  saveTestimony(testimony: Testimony, image: File): Observable<Testimony> {
    const formData = new FormData();
    testimony.id = 0;
    testimony.photoLink = "";
    formData.append("file", image, image.name);
    formData.append("testimony", JSON.stringify(testimony));
    return this.httpClient.post<any>(this.baseApi + "testimonies", formData);
  }
}
