import {
  FormGroup,
  FormControl,
  Validators
} from "@angular/forms";
import { TeamService } from "./../../../../services/team.service";
import { Component, OnInit } from "@angular/core";
import { Employee } from "src/app/models/Employee";
import { ActivatedRoute, ParamMap, Router } from "@angular/router";
import { mimeType } from "src/app/models/file-type.validator";

@Component({
  selector: "app-employee-details",
  templateUrl: "./employee-details.component.html",
  styleUrls: ["./employee-details.component.css"]
})
export class EmployeeDetailsComponent implements OnInit {
  isLoading = true;
  employee: Employee;
  employeeId: number;
  imgError = false;
  form: FormGroup;
  reader: FileReader;
  imagePreview: string;
  image: File;

  constructor(
    private httpTeamService: TeamService,
    private route: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {
    this.route.paramMap.subscribe((paramMap: ParamMap) => {
      if (paramMap.has("employeeId")) {
        this.employeeId = +paramMap.get("employeeId");
        this.getEmployeeById();
      }
    });
  }

  onImagePicked(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file });
    this.reader = new FileReader();
    this.reader.onload = () => {
      if (this.form.get("image").valid) {
        this.imgError = false;
        this.imagePreview = this.reader.result as string;
        this.image = file;
      } else {
        this.imgError = true;
        return;
      }
    };
    try {
      this.reader.readAsDataURL(file);
    } catch (err) {
      console.log(err);
    }
  }

  activateFormVerify() {
    this.form = new FormGroup({
      firstName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      lastName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      role: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(5)]
      }),
      description: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(10)]
      }),
      image: new FormControl(null, {
        validators: [Validators.required],
        asyncValidators: [mimeType]
      })
    });
  }

  getEmployeeById() {
    this.httpTeamService.getEmployee(this.employeeId).subscribe(
      data => {
        this.employee = data;
      },
      error => console.log(error),
      () => {
        this.imagePreview = this.employee.pictureLink;
        this.activateFormVerify();
        this.setDefaultValues();
        this.isLoading = false;
      }
    );
  }

  saveEmployee() {
    if (this.form.invalid) {
      if (this.form.get("image").invalid) {
        this.imgError = true;
      }
      return;
    }
    const emp = this.checkFormValues(this.employee);
    if (emp) {
      this.isLoading = true;
      this.httpTeamService.updateEmployee(emp, this.image).subscribe(
        data => {
        },
        error => {
          console.log(error);
          this.isLoading = false;
        },
        () => {
          this.router.navigate(["admin/team"]);
        }
      );
    } else {
      // TODO msg erreur
    }
  }

  checkFormValues(emp: Employee): Employee {
    // TODO test valeurs
    emp.firstName = this.form.value.firstName;
    emp.lastName = this.form.value.lastName;
    emp.role = this.form.value.role;
    return emp;
  }

  setDefaultValues() {
    this.form.setValue({
      firstName: this.employee.firstName,
      lastName: this.employee.lastName,
      role: this.employee.role,
      description: this.employee.description,
      image: "null"
    });
  }
}
