import { OfferedService } from '../models/OfferedService';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ServiceOfferedService {
  private baseApi = environment.baseApi;

  constructor(private httpClient: HttpClient) { }

  getOfferedServices(): Observable<OfferedService[]> {
    return this.httpClient.get<OfferedService[]>(this.baseApi + 'offeredServices');
  }

  getOfferedServiceById(id: number): Observable<OfferedService> {
    return this.httpClient.get<OfferedService>(this.baseApi + 'offeredServices/' + id);
  }

  updateOfferedService(offService: OfferedService): Observable<OfferedService> {
    return this.httpClient.put<OfferedService>(this.baseApi + 'offeredServices', offService);
  }

  addOfferedService(offService: OfferedService): Observable<any> {
    return this.httpClient.post<any>(this.baseApi + 'offeredServices', offService);
  }

  deleteOfferedService(offServiceId: number): Observable<OfferedService> {
    return this.httpClient.delete<OfferedService>(this.baseApi + 'offeredServices/' + offServiceId);
  }
}
