import { Component, OnInit } from "@angular/core";
import { CompanyService } from "src/app/services/company.service";
import { Company } from "src/app/models/Company";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { Router } from "@angular/router";

@Component({
  selector: "app-company-info-admin",
  templateUrl: "./company-info-admin.component.html",
  styleUrls: ["./company-info-admin.component.css"]
})
export class CompanyInfoAdminComponent implements OnInit {
  company: Company;
  isLoading = true;
  form: FormGroup;
  error = false;

  constructor(private httpCompany: CompanyService, private router: Router) {}

  ngOnInit() {
    this.getCompany();
  }

  updateContactAdmin() {
    if (this.form.invalid) {
      return;
    }
    const updatedCompany = this.checkValuesOfCompanyForm(this.company);
    this.updateCompany(updatedCompany);
  }

  checkValuesOfCompanyForm(company: Company): Company {
    // TODO ajouter des tests de vrification des donnes
    company.companyName = this.form.value.companyName;
    company.phoneNumber = this.form.value.phoneNumber;
    company.email = this.form.value.email;
    company.address.streetNumber = this.form.value.streetNumber;
    company.address.streetName = this.form.value.streetName;
    company.address.city = this.form.value.city;
    company.address.postalCode = this.form.value.postalCode;
    company.address.country = this.form.value.country;
    company.address.province = this.form.value.province;
    return company;
  }

  activateFormVerify() {
    this.form = new FormGroup({
      companyName: new FormControl(this.company.companyName, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      phoneNumber: new FormControl(this.company.phoneNumber, {
        validators: [Validators.required, Validators.minLength(10)]
      }),
      email: new FormControl(this.company.email, {
        validators: [Validators.required, Validators.minLength(8)]
      }),
      streetNumber: new FormControl(this.company.address.streetNumber, {
        validators: [Validators.required, Validators.minLength(1)]
      }),
      streetName: new FormControl(this.company.address.streetName, {
        validators: [Validators.required, Validators.minLength(3)]
      }),
      city: new FormControl(this.company.address.city, {
        validators: [Validators.required, Validators.minLength(5)]
      }),
      postalCode: new FormControl(this.company.address.postalCode, {
        validators: [Validators.required, Validators.minLength(6)]
      }),
      country: new FormControl(this.company.address.country, {
        validators: [Validators.required, Validators.minLength(3)]
      }),
      province: new FormControl(this.company.address.country, {
        validators: [Validators.required, Validators.minLength(2)]
      })
    });
  }

  getCompany() {
    this.httpCompany.getCompany().subscribe(
      data => {
        this.company = data;
      },
      error => {
        console.log(error);
      },
      () => {
        this.isLoading = false;
        this.activateFormVerify();
      }
    );
  }
  updateCompany(companyUpdated: Company) {
    this.isLoading = true;
    let success = false;
    this.httpCompany.updateCompany(companyUpdated).subscribe(
      data => {
        if (data.message === "SUCCESS") {
          success = true;
        } else {
          success = false;
        }
      },
      error => {
        console.log(error);
        this.isLoading = false;
      },
      () => {
        if (success) {
          this.router.navigate(["admin/homepage"]);
        } else {
          this.error = true;
          this.isLoading = false;
        }
      }
    );
  }
}
