import { ServiceOfferedService } from "./../../services/service-offered.service";
import { OfferedService } from "../../models/OfferedService";
import { Testimony } from "./../../models/Testimony";
import { Component, OnInit } from "@angular/core";
import { Image } from "angular2_photoswipe";
import { ProjectService } from "src/app/services/project.service";
import { TestimonyService } from "src/app/services/testimony.service";
import { Project } from "src/app/models/Project";
import { environment } from "src/environments/environment";
import { Picture } from "src/app/models/Picture";
import { Router } from "@angular/router";
@Component({
  selector: "app-homepage",
  templateUrl: "./homepage.component.html",
  styleUrls: ["./homepage.component.css"]
})
export class HomepageComponent implements OnInit {
  listeProjects: Project[] = [];
  listeTestimonies: Testimony[] = [];
  listeOfferedServices: OfferedService[] = [];
  isLoading = true;

  // tslint:disable-next-line: max-line-length
  constructor(
    private httpProject: ProjectService,
    // tslint:disable-next-line: align
    private httpTestimonies: TestimonyService,
    // tslint:disable-next-line: align
    private httpOfferedServices: ServiceOfferedService,
    private router: Router
  ) {}

  ngOnInit() {
    this.getProjectsSliced();
    this.getTestimonies();
    this.getOfferedServices();
  }

  getProjectsSliced() {
    this.httpProject.getProjects().subscribe(
      data => {
        data.slice(0, 4).forEach((project: Project) => {
          const pr: Project = new Project();
          pr.id = project.id;
          pr.projectName = project.projectName;
          pr.description = project.description;
          pr.listePictures = [];
          project.listePictures.forEach((picture: Picture) => {
            const pic: Picture = new Picture();
            pic.id = picture.id;
            pic.descriptionPhoto = picture.descriptionPhoto;
            pic.lienPhoto = environment.photoBaseApi + picture.lienPhoto;
            pic.lienPhotoTn = environment.photoBaseApi + picture.lienPhotoTn;
            pic.nomPhoto = picture.nomPhoto;
            pr.listePictures.push(pic);
          });
          this.listeProjects.push(pr);
        });
        this.addPhotoSwipe();
      },
      error => {
        console.log(error);
      },
      () => {
        this.isLoading = false;
      }
    );
  }

  addPhotoSwipe() {
    this.listeProjects.forEach(element => {
      let count = 0;
      element.listePictures.forEach(photo => {
        let img = document.createElement("img");
        img.src = photo.lienPhoto;
        img.onload = () => {
          photo.picture.height = img.height;
          photo.picture.width = img.width;
        };
        photo.picture = new Image();
        photo.picture.largeUrl = photo.lienPhoto;
        photo.picture.height = img.height;
        photo.picture.width = img.width;
        photo.picture.id = count;
        photo.picture.size = `${photo.picture.width}x${photo.picture.height}`;
        photo.picture.thumbUrl = photo.lienPhotoTn;
        photo.picture.description = photo.descriptionPhoto;
        count += 1;
      });
    });
  }

  getTestimonies() {
    this.httpTestimonies.getTestimonies().subscribe(data => {
      data.forEach((testimony) => {
        const tes = new Testimony();
        tes.id = testimony.id;
        tes.testimonyFName = testimony.testimonyFName;
        tes.testimonyLName = testimony.testimonyLName;
        tes.relation = testimony.relation;
        tes.comment = testimony.comment;
        tes.photoLink = environment.photoBaseApi + testimony.photoLink;
        this.listeTestimonies.push(tes);
      });
    });
  }

  getOfferedServices() {
    this.httpOfferedServices.getOfferedServices().subscribe(
      data => {
        this.listeOfferedServices = data;
      },
      error => {
        console.log(error);
      },
      () => {
        this.isLoading = false;
      }
    );
  }
}
