import { WindowScrolling } from "./../../../models/WindowScrolling";
import { Component, OnInit } from "@angular/core";
import { ContactDemandService } from "src/app/services/contact-demand.service";
import { QuoteService } from "src/app/services/quote.service";
import { ContactDemand } from "src/app/models/ContactDemand";
import { Quote } from "src/app/models/Quote";
import { Subject, Subscription } from 'rxjs';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: "app-adminheader",
  templateUrl: "./adminheader.component.html",
  styleUrls: ["./adminheader.component.css"]
})
export class AdminheaderComponent implements OnInit {

  private isAuthenticated = false;
  private token: string;
  private userId: string;
  private authStatusListener = new Subject<boolean>();
  cdExists = false;
  quotesExists = false;
  public listContactDemands: ContactDemand[] = [];
  public listQuotes: Quote[] = [];
  authStatusSub: Subscription;

  getToken() {
    return this.token;
  }

  getIsAuth() {
    return this.isAuthenticated;
  }

  getUserId() {
    return this.userId;
  }

  getAuthStatusListener() {
    return this.authStatusListener.asObservable();
  }

  constructor(
    private windowScrolling: WindowScrolling,
    private httpContactDemand: ContactDemandService,
    private httpQuotes: QuoteService,
    public authService: AuthService
  ) {}

  ngOnInit() {
    this.windowScrolling.disable();
    this.windowScrolling.enable();
    this.getContactDemandsCount();
    this.getQuotesCount();
    this.authStatusSub = this.authService.getAuthStatusListener().subscribe();
  }

  logOut() {
    this.windowScrolling.disable();
    this.windowScrolling.enable();
    this.authService.logout();
  }

  getContactDemandsCount() {
    this.httpContactDemand.getContactDemands().subscribe(
      data => {
        this.listContactDemands = data;
      },
      error => console.log(error),
      () => {
        if (this.listContactDemands.length > 0) {
          this.cdExists = true;
        }
      }
    );
  }

  getQuotesCount() {
    this.httpQuotes.getQuotes().subscribe(
      data => {
        this.listQuotes = data;
      },
      error => console.log(error),
      () => {
        if (this.listQuotes.length > 0) {
          this.quotesExists = true;
        }
      }
    );
  }

  enableScrolling() {
    this.windowScrolling.disable();
    this.windowScrolling.enable();
    const checkButton = document.querySelector("#check") as HTMLInputElement;
    checkButton.checked = false;
    document.querySelector("#menuBtn").classList.add("fa-bars");
    document.querySelector("#menuBtn").classList.remove("fa-times");
  }

  disableScrolling() {
    this.windowScrolling.disable();
  }

  setClickListener(event: Event) {
    const inputMenu = event.target as HTMLInputElement;
    if (inputMenu.checked) {
      this.disableScrolling();
      document.querySelector("#menuBtn").classList.remove("fa-bars");
      document.querySelector("#menuBtn").classList.add("fa-times");
    } else {
      this.enableScrolling();
      document.querySelector("#menuBtn").classList.add("fa-bars");
      document.querySelector("#menuBtn").classList.remove("fa-times");
    }
  }
}
