import { Component, OnInit } from '@angular/core';
import { Project } from 'src/app/models/Project';
import { ProjectService } from 'src/app/services/project.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-projects-admin',
  templateUrl: './projects-list-admin.component.html',
  styleUrls: ['./projects-list-admin.component.css']
})
export class ProjectsListAdminComponent implements OnInit {
  listeProjects: Project[] = [];
  isLoading = true;

  constructor(private httpProject: ProjectService, private router: Router) { }

  ngOnInit() {
    this.getProjects();
  }

  getProjects() {
    this.httpProject.getProjects().subscribe(data => {
      this.listeProjects = data;
    }, error => console.log(error), () => {
      this.isLoading = false;
    });
  }

  openProjectDetails(projectId: number) {
    this.router.navigate(['admin/projects/' + projectId]);
  }

}
