import { Component, OnInit } from "@angular/core";
import { ContactDemand } from "src/app/models/ContactDemand";
import { ContactDemandService } from "src/app/services/contact-demand.service";
import { Router } from "@angular/router";

@Component({
  selector: "app-contacts-list-admin",
  templateUrl: "./contacts-list-admin.component.html",
  styleUrls: ["./contacts-list-admin.component.css"]
})
export class ContactsListAdminComponent implements OnInit {
  isLoading = true;
  listContactDemand: ContactDemand[] = [];
  selectOption = ["incomplete", "complete"];
  selectedOption = this.selectOption[0]; 
  constructor(
    private httpContactDemand: ContactDemandService
  ) {}

  ngOnInit() {
    this.getContactDemands();
  }

  completeCd(cd: ContactDemand) {
    cd.completed = true;
    this.isLoading = true;
    this.httpContactDemand.markCdAsCompleted(cd).subscribe(data => {
      console.log(data);
    },error => {
      console.log(error);
      this.isLoading = false;
    }, ()=> {
      this.getContactDemands();
      this.isLoading = false;
    });
  }

  selectContactDemandsOnChange(value:string) {
    if (value === "incomplete") {
      this.isLoading = true;
      this.selectedOption = this.selectOption[0]; 
      this.getContactDemands();
    }

    if (value === "complete") {
      this.isLoading = true;
      this.selectedOption = this.selectOption[1]; 
      this.getContactDemandsCompleted();
    }
  }

  getContactDemands() {
    this.httpContactDemand.getContactDemands().subscribe(
      data => {
        this.listContactDemand = data;
      },
      error => console.log(error),
      () => {
        this.isLoading = false;
      }
    );
  }

  getContactDemandsCompleted() {
    this.httpContactDemand.getContactDemandsCompleted().subscribe(
      data => {
        this.listContactDemand = data;
      },
      error => console.log(error),
      () => {
        this.isLoading = false;
      }
    );
  }
}
