import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Quote } from '../models/Quote';

@Injectable({
  providedIn: 'root'
})
export class QuoteService {
  private baseApi = environment.baseApi;

  constructor(private httpClient: HttpClient) { }

  postQuote(quote: Quote, picturesList: File[]): Observable<any> {
    const formData = new FormData();
    Array.from(picturesList).map((file, index) => {
      return formData.append("file" + index, file, file.name);
    });
    formData.append("quote", JSON.stringify(quote));
    return this.httpClient.post<any>(this.baseApi + "quotes", formData);
  }

  markQuoteCompleted(quote: Quote): Observable<any> {
    return this.httpClient.put<any>(this.baseApi + "quotes/" + quote.id, quote);
  }

  getQuotes(): Observable<Quote[]> {
    return this.httpClient.get<Quote[]>(`${this.baseApi}quotes`);
  }

  getQuoteById(quoteId: number): Observable<Quote> {
    return this.httpClient.get<Quote>(`${this.baseApi}quotes/${quoteId}`);
  }

  getQuotesCompleted(): Observable<Quote[]> {
    return this.httpClient.get<Quote[]>(this.baseApi + "quotes/completed");
  }
}
