
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Admin } from '../../../models/Admin';
import { Subscription } from 'rxjs';
import { Employee } from 'src/app/models/Employee';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-auth-page',
  templateUrl: './auth-page.component.html',
  styleUrls: ['./auth-page.component.css']
})
export class AuthPageComponent implements OnInit {
  form: FormGroup;
  isLoading = false;
  errorUser = false;
  authStatusSub: Subscription;

  constructor(public authService: AuthService) { }

  ngOnInit() {
    this.authStatusSub = this.authService.getAuthStatusListener().subscribe();
    this.activateFormVerify();
  }

  activateFormVerify() {
    this.form = new FormGroup({
      username: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(5)]
      }),
      password: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(8)]
      })
    });
  }

  logIn() {
    if (this.form.invalid) {
      return;
    }
    this.authenticate();

  }

  authenticate() {
    this.isLoading = true;
    this.errorUser = false;
    let adminAccount = new Admin();
    adminAccount.username = this.form.value.username;
    adminAccount.password = this.form.value.password;
    adminAccount.employee = new Employee();
    this.authService.login(adminAccount);
    setTimeout(() => {
      this.errorUser = true;
      this.isLoading = false;
    }, 3000);

  }

  ngOnDestroy() {
    this.authStatusSub.unsubscribe();
  }
}
