import { QuoteService } from 'src/app/services/quote.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Quote } from 'src/app/models/Quote';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-quote-details-admin',
  templateUrl: './quote-details-admin.component.html',
  styleUrls: ['./quote-details-admin.component.css']
})
export class QuoteDetailsAdminComponent implements OnInit {

  isLoading = true;
  form: FormGroup;
  quote: Quote;
  constructor(
    private httpQuote: QuoteService,
    private router: Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.activateForm();
    this.route.paramMap.subscribe((paramMap: ParamMap) => {
      if (paramMap.has("quoteId")) {
        const quoteId = +paramMap.get("quoteId");
        this.getQuoteById(quoteId);
      }
    });
  }

  getQuoteById(quoteId: number) {
    this.httpQuote.getQuoteById(quoteId).subscribe(
      data => {
        this.quote = data;
        this.quote.fileLinks.forEach(element => {
          element.fileLink = environment.photoBaseApi + element.fileLink;
        });
      },
      error => console.log(error),
      () => {
        this.isLoading = false;
      }
    );
  }

  saveQuote() {
    if (this.form.invalid) {
      return;
    }
    this.checkFormValues();
    this.httpQuote.markQuoteCompleted(this.quote).subscribe(
      data => {
      },
      error => console.log(error),
      () => {
        // TODO navigate ailleurs
        this.router.navigate(["/admin/quotes"]);
      }
    );
  }

  checkFormValues() {
    this.quote.answer = this.form.value.answer;
  }

  activateForm() {
    this.form = new FormGroup({
      answer: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      })
    });
  }
}
