import { AddOfferedServiceComponent } from "./admin/components/offeredServices/add-offered-service/add-offered-service.component";
import { OfferedServicesListComponent } from "./admin/components/offeredServices/offered-services-list/offered-services-list.component";
import { AddTestimonyComponent } from "./admin/components/testimonies/add-testimony/add-testimony.component";
import { EmployeeDetailsComponent } from "./admin/components/team/employee-details/employee-details.component";
import { AddEmployeeComponent } from "./admin/components/team/add-employee/add-employee.component";
import { QuotingComponent } from "./components/quoting/quoting.component";
import { ProjectsComponent } from "./components/projects/projects.component";
import { OfferedServicesComponent } from "./components/offered-services/offered-services.component";
import { ContactComponent } from "./components/contact/contact.component";
import { AboutComponent } from "./components/about/about.component";
import { HomepageComponent } from "./components/homepage/homepage.component";
import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AuthPageComponent } from "./admin/components/auth-page/auth-page.component";
import { HomepageAdminComponent } from "./admin/components/homepage-admin/homepage-admin.component";
import { CompanyInfoAdminComponent } from "./admin/components/company-info-admin/company-info-admin.component";
import { ContactsListAdminComponent } from "./admin/components/contact/contacts-list-admin/contacts-list-admin.component";
import { ContactDetailsAdminComponent } from "./admin/components/contact/contact-details-admin/contact-details-admin.component";
import { ProjectsListAdminComponent } from "./admin/components/projects/projects-list-admin/projects-list-admin.component";
import { ProjectDetailsAdminComponent } from "./admin/components/projects/project-details-admin/project-details-admin.component";
import { QuotesListAdminComponent } from "./admin/components/quotes/quotes-list-admin/quotes-list-admin.component";
import { TeamListAdminComponent } from "./admin/components/team/team-list-admin/team-list-admin.component";
import { QuoteDetailsAdminComponent } from "./admin/components/quotes/quote-details-admin/quote-details-admin.component";
import { TestimoniesComponent } from "./admin/components/testimonies/testimonies.component";
import { TestimonyDetailsComponent } from "./admin/components/testimonies/testimony-details/testimony-details.component";
import { AddProjectComponent } from "./admin/components/projects/add-project/add-project.component";
import { OfferedServicesDetailsComponent } from "./admin/components/offeredServices/offered-services-details/offered-services-details.component";
import { AuthGuard } from './models/Auth.guard';

const routes: Routes = [
  { path: "", component: HomepageComponent },
  { path: "about", component: AboutComponent },
  { path: "contact", component: ContactComponent },
  { path: "offered-services", component: OfferedServicesComponent },
  { path: "projects", component: ProjectsComponent },
  { path: "quoting", component: QuotingComponent },
  { path: "admin/auth", component: AuthPageComponent },
  { path: "admin/homepage", component: HomepageAdminComponent, canActivate: [AuthGuard] },
  { path: "admin/company-info", component: CompanyInfoAdminComponent, canActivate: [AuthGuard] },
  { path: "admin/contacts", component: ContactsListAdminComponent, canActivate: [AuthGuard] },
  {
    path: "admin/contactDemandDetails/:contactId",
    component: ContactDetailsAdminComponent,
    canActivate: [AuthGuard]
  },
  { path: "admin/projects", component: ProjectsListAdminComponent, canActivate: [AuthGuard] },
  {
    path: "admin/projects/:projectId",
    component: ProjectDetailsAdminComponent,
    canActivate: [AuthGuard]
  },
  { path: "admin/quotes", component: QuotesListAdminComponent, canActivate: [AuthGuard] },
  { path: "admin/quoteDetails/:quoteId", component: QuoteDetailsAdminComponent, canActivate: [AuthGuard] },
  { path: "admin/team", component: TeamListAdminComponent, canActivate: [AuthGuard] },
  { path: "admin/testimonies", component: TestimoniesComponent, canActivate: [AuthGuard] },
  { path: "admin/addEmployee", component: AddEmployeeComponent, canActivate: [AuthGuard] },
  { path: "admin/addTestimony", component: AddTestimonyComponent, canActivate: [AuthGuard] },
  {
    path: "admin/employeeDetails/:employeeId",
    component: EmployeeDetailsComponent,
    canActivate: [AuthGuard]
  },
  {
    path: "admin/testimonyDetails/:testimonyId",
    component: TestimonyDetailsComponent,
    canActivate: [AuthGuard]
  },
  {
    path: "admin/offeredServicesList",
    component: OfferedServicesListComponent,
    canActivate: [AuthGuard]
  },
  { path: "admin/addOfferedService", component: AddOfferedServiceComponent, canActivate: [AuthGuard] },
  {
    path: "admin/offeredServicesDetails/:serviceId",
    component: OfferedServicesDetailsComponent,
    canActivate: [AuthGuard]
  },
  {
    path: "admin/addProject", component: AddProjectComponent,
    canActivate: [AuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
