import { Injectable } from "@angular/core";
import { environment } from "src/environments/environment";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { ContactDemand } from "../models/ContactDemand";

@Injectable({
  providedIn: "root"
})
export class ContactDemandService {
  private baseApi = environment.baseApi;

  constructor(private httpClient: HttpClient) {}

  postContactDemand(contactDemand: ContactDemand): Observable<any> {
    console.log(contactDemand);
    return this.httpClient.post<any>(
      this.baseApi + "contactDemand",
      contactDemand
    );
  }

  getContactDemands(): Observable<ContactDemand[]> {
    return this.httpClient.get<ContactDemand[]>(this.baseApi + "contactDemand");
  }

  getContactDemandsCompleted(): Observable<ContactDemand[]> {
    return this.httpClient.get<ContactDemand[]>(this.baseApi + "contactDemand/completed");
  }

  getContactDemandById(id: number): Observable<ContactDemand> {
    return this.httpClient.get<ContactDemand>(
      this.baseApi + "contactDemand/" + id
    );
  }

  updateContactDemand(contactDemand: ContactDemand): Observable<any> {
    return this.httpClient.put<any>(
      this.baseApi + "contactDemand/" + contactDemand.id,
      contactDemand
    );
  }

  markCdAsCompleted(contactDemand: ContactDemand): Observable<any> {
    console.log(contactDemand);
    return this.httpClient.put<any>(
      this.baseApi + "contactDemand/" + contactDemand.id,
      contactDemand
    );
  }
}
