import { ActivatedRoute, Router, ParamMap } from "@angular/router";
import { Component, OnInit } from "@angular/core";
import { ServiceOfferedService } from "src/app/services/service-offered.service";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { OfferedService } from "src/app/models/OfferedService";

@Component({
  selector: "app-offered-services-details",
  templateUrl: "./offered-services-details.component.html",
  styleUrls: ["./offered-services-details.component.css"]
})
export class OfferedServicesDetailsComponent implements OnInit {
  isLoading = true;
  form: FormGroup;
  offeredService: OfferedService;
  constructor(
    private httpOfferedServices: ServiceOfferedService,
    private router: Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.activateForm();
    this.route.paramMap.subscribe((paramMap: ParamMap) => {
      if (paramMap.has("serviceId")) {
        const offeredServiceId = +paramMap.get("serviceId");
        this.getOfferedServiceId(offeredServiceId);
      }
    });
  }

  getOfferedServiceId(offId: number) {
    this.httpOfferedServices.getOfferedServiceById(offId).subscribe(
      data => {
        this.offeredService = data;
      },
      error => console.log(error),
      () => {
        this.setDefaultValues();
        this.isLoading = false;
      }
    );
  }

  setDefaultValues() {
    this.form.setValue({
      serviceName: this.offeredService.serviceName,
      serviceDescription: this.offeredService.serviceDescription
    });
  }

  saveOfferedService() {
    if (this.form.invalid) {
      return;
    }
    this.checkFormValues();
    this.isLoading = true;
    this.httpOfferedServices
      .updateOfferedService(this.offeredService)
      .subscribe(
        data => {
        },
        error => {
          console.log(error);
          this.isLoading = false;
        },
        () => {
          // TODO navigate ailleurs
          this.router.navigate(["/admin/offeredServicesList"]);
        }
      );
  }

  checkFormValues() {
    this.offeredService.serviceName = this.form.value.serviceName;
    this.offeredService.serviceDescription = this.form.value.serviceDescription;
  }
  activateForm() {
    this.form = new FormGroup({
      serviceName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      serviceDescription: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      })
    });
  }
}
