import { TestimonyService } from "src/app/services/testimony.service";
import { Component, OnInit } from "@angular/core";
import { Testimony } from "src/app/models/Testimony";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { ActivatedRoute, ParamMap, Router } from "@angular/router";
import { mimeType } from "src/app/models/file-type.validator";

@Component({
  selector: "app-testimony-details",
  templateUrl: "./testimony-details.component.html",
  styleUrls: ["./testimony-details.component.css"]
})
export class TestimonyDetailsComponent implements OnInit {
  isLoading = true;
  testimony: Testimony;
  testimonyId: number;
  imgError = false;
  form: FormGroup;
  reader: FileReader;
  imagePreview: string;
  image: File;

  constructor(
    private httpTestimony: TestimonyService,
    private route: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {
    this.route.paramMap.subscribe((paramMap: ParamMap) => {
      if (paramMap.has("testimonyId")) {
        this.testimonyId = +paramMap.get("testimonyId");
        this.getTestimonyById();
      }
    });
  }

  onImagePicked(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file });
    this.reader = new FileReader();
    this.reader.onload = () => {
      if (this.form.get("image").valid) {
        this.imgError = false;
        this.imagePreview = this.reader.result as string;
        this.image = file;
      } else {
        this.imgError = true;
        return;
      }
    };
    try {
      this.reader.readAsDataURL(file);
    } catch (err) {
      console.log(err);
    }
  }

  activateFormVerify() {
    this.form = new FormGroup({
      testimonyFName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      testimonyLName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      comment: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(5)]
      }),
      relation: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(10)]
      }),
      image: new FormControl(null, {
        validators: [Validators.required],
        asyncValidators: [mimeType]
      })
    });
  }

  getTestimonyById() {
    this.httpTestimony.getTestimony(this.testimonyId).subscribe(
      data => {
        this.testimony = data;
      },
      error => console.log(error),
      () => {
        this.imagePreview = this.testimony.photoLink;
        this.activateFormVerify();
        this.setDefaultValues();
        this.isLoading = false;
      }
    );
  }

  saveTestimony() {
    if (this.form.invalid) {
      if (this.form.get("image").invalid) {
        this.imgError = true;
      }
      return;
    }
    const tes = this.checkFormValues(this.testimony, this.form);
    if (tes) {
      this.httpTestimony.updateTestimony(tes, this.image).subscribe(
        data => {
          // TODO check data
        },
        error => console.log(error),
        () => {
          // TODO NAVIGATEs
          this.router.navigate(["/admin/testimonies"]);
        }
      );
    }
  }

  checkFormValues(testimony: Testimony, form: FormGroup): Testimony {
    testimony.testimonyFName = form.value.testimonyFName;
    testimony.testimonyLName = form.value.testimonyLName;
    testimony.relation = form.value.relation;
    testimony.comment = form.value.comment;

    return testimony;
  }

  setDefaultValues() {
    this.form.setValue({
      testimonyFName: this.testimony.testimonyFName,
      testimonyLName: this.testimony.testimonyLName,
      comment: this.testimony.comment,
      relation: this.testimony.relation,
      image: "null"
    });
  }
}
