import { ProjectService } from "./../../../../services/project.service";
import { Component, OnInit } from "@angular/core";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { mimeType } from "src/app/models/file-type.validator";
import { Picture } from "src/app/models/Picture";
import { Project } from "src/app/models/Project";
import { Router } from "@angular/router";

@Component({
  selector: "app-add-project",
  templateUrl: "./add-project.component.html",
  styleUrls: ["./add-project.component.css"]
})
export class AddProjectComponent implements OnInit {
  isLoading = true;
  form: FormGroup;
  imagePreview: string;
  pictures: Picture[] = [];
  imgError = false;
  reader: FileReader;
  project: Project;
  files: File[];

  constructor(
    private httpProjectService: ProjectService,
    private router: Router
  ) {}

  ngOnInit() {
    this.activateForm();
    this.project = new Project();
    this.project.listePictures = [];
    this.files = [];
  }

  onImagePicked(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file });
    this.reader = new FileReader();
    this.reader.onload = () => {
      if (this.form.get("image").valid) {
        this.imgError = false;
        this.imagePreview = this.reader.result as string;
        this.files.push(file);
        this.addPicture(file, this.imagePreview);
      } else {
        this.imgError = true;
        this.form.get("image").setValue("empty");
      }
    };
    this.reader.readAsDataURL(file);
  }

  addPicture(file: File, imagePreview: string) {
    const pic = new Picture();
    pic.lienPhoto = imagePreview;
    pic.nomPhoto = this.createUniqueName(file);
    pic.descriptionPhoto = "";
    pic.id = 0;
    this.project.listePictures.push(pic);
  }

  createUniqueName(file: File) {
    const fileNameArray = file.name;
    return fileNameArray;
  }

  activateForm() {
    this.form = new FormGroup({
      projectName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      description: new FormControl(""),
      image: new FormControl(null, {
        validators: [Validators.required],
        asyncValidators: [mimeType]
      }),
      files: new FormControl()
    });
    this.isLoading = false;
  }

  saveProject() {
    if (this.form.invalid) {
      if (this.form.get("image").invalid) {
        this.imgError = true;
      }
      return;
    }
    this.imgError = false;
    this.isLoading = true;
    if (this.checkValues(this.project)) {
      this.httpProjectService.saveProject(this.project, this.files).subscribe(
        data => {
        },
        error => {
          console.log(error);
          this.isLoading = false;
        },
        () => {
          this.isLoading = false;
          this.router.navigate(["/admin/projects"]);
        }
      );
    }
  }

  checkValues(project: Project): boolean {
    const projectName = this.form.value.projectName;
    const projectDescription = this.form.value.description;
    project.projectName = projectName;
    project.description = projectDescription;
    project.id = 0;
    return true;
  }
}
