import { Component, OnInit } from "@angular/core";
import { Testimony } from "src/app/models/Testimony";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { TestimonyService } from "src/app/services/testimony.service";
import { Router } from "@angular/router";
import { mimeType } from "src/app/models/file-type.validator";

@Component({
  selector: "app-add-testimony",
  templateUrl: "./add-testimony.component.html",
  styleUrls: ["./add-testimony.component.css"]
})
export class AddTestimonyComponent implements OnInit {
  isLoading = true;
  testimony: Testimony;
  testimonyId: number;
  imgError = false;
  form: FormGroup;
  reader: FileReader;
  imagePreview: string;
  image: File;

  constructor(
    private httpTestimony: TestimonyService,
    private router: Router
  ) { }

  ngOnInit() {
    this.activateFormVerify();
    this.isLoading = false;
  }

  onImagePicked(event: Event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({ image: file });

    this.reader = new FileReader();
    this.reader.onload = () => {
      if (this.form.get("image").valid) {
        this.imgError = false;
        this.imagePreview = this.reader.result as string;
        this.image = file;
      } else {
        this.imgError = true;
        return;
      }
    };
    try {
      this.reader.readAsDataURL(file);
    } catch (err) {
      console.log(err);
    }
  }

  activateFormVerify() {
    this.form = new FormGroup({
      testimonyFName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      testimonyLName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      comment: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(5)]
      }),
      relation: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(10)]
      }),
      image: new FormControl(null, {
        validators: [Validators.required],
        asyncValidators: [mimeType]
      })
    });
  }

  saveTestimony() {
    if (this.form.invalid) {
      if (this.form.get("image").invalid) {
        this.imgError = true;
      }
      return;
    }
    this.checkFormInfos();
    this.isLoading = true;
    this.httpTestimony.saveTestimony(this.testimony, this.image).subscribe(
      data => {
        // TODO
      },
      error => {
        console.log(error);
        this.isLoading = false;
      },
      () => {
        this.isLoading = false;
        this.router.navigate(["admin/testimonies"]);
      }
    );
  }

  checkFormInfos() {
    this.testimony = new Testimony();
    this.testimony.testimonyFName = this.form.value.testimonyFName;
    this.testimony.testimonyLName = this.form.value.testimonyLName;
    this.testimony.comment = this.form.value.comment;
    this.testimony.relation = this.form.value.relation;
    this.testimony.id = null;
    this.testimony.photoLink = null;
  }
}
