import { ServiceOfferedService } from "./../../../../services/service-offered.service";
import { Component, OnInit, OnDestroy } from "@angular/core";
import { FormGroup, FormControl, Validators } from "@angular/forms";
import { OfferedService } from "src/app/models/OfferedService";
import { Router } from "@angular/router";

@Component({
  selector: "app-add-offered-service",
  templateUrl: "./add-offered-service.component.html",
  styleUrls: ["./add-offered-service.component.css"]
})
export class AddOfferedServiceComponent implements OnInit, OnDestroy {
  isLoading = true;
  form: FormGroup;
  offeredService: OfferedService;
  constructor(
    private httpOfferedServices: ServiceOfferedService,
    private router: Router
  ) {}

  ngOnInit() {
    this.activateForm();
    this.isLoading = false;
  }

  ngOnDestroy() {}

  activateForm(): void {
    this.form = new FormGroup({
      serviceName: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(2)]
      }),
      serviceDescription: new FormControl(null, {
        validators: [Validators.required, Validators.minLength(5)]
      })
    });
  }

  saveOfferedService() {
    if (this.form.invalid) {
      return;
    }

    this.isLoading = true;
    this.offeredService = new OfferedService();
    this.offeredService.serviceName = this.form.value.serviceName;
    this.offeredService.serviceDescription = this.form.value.serviceDescription;
    this.httpOfferedServices.addOfferedService(this.offeredService).subscribe(
      data => {
      },
      error => {
        console.log(error);
        this.isLoading = false;
      },
      () => {
        // TODO navigate ailleurs
        this.router.navigate(["/admin/offeredServicesList"]);
      }
    );
  }
}
