import { Component, OnInit } from '@angular/core';
import { OfferedService } from 'src/app/models/OfferedService';
import { ServiceOfferedService } from 'src/app/services/service-offered.service';

@Component({
  selector: 'app-offered-services',
  templateUrl: './offered-services.component.html',
  styleUrls: ['./offered-services.component.css']
})
export class OfferedServicesComponent implements OnInit {
  listeOfferedServices: OfferedService[] = [];
  constructor(private httpOfferedServices: ServiceOfferedService) { }
  isLoading = true;

  ngOnInit() {
    this.getOfferedServices();
  }

  getOfferedServices() {
    this.httpOfferedServices.getOfferedServices().subscribe(data => {
      this.listeOfferedServices = data;
    }, error => {console.log(error);}, () =>{this.isLoading = false;} );
  }

}
