import { environment } from './../../../../environments/environment.prod';
import { TestimonyService } from "./../../../services/testimony.service";
import { Component, OnInit } from "@angular/core";
import { Testimony } from "src/app/models/Testimony";

@Component({
  selector: "app-testimonies",
  templateUrl: "./testimonies.component.html",
  styleUrls: ["./testimonies.component.css"]
})
export class TestimoniesComponent implements OnInit {
  listeTestimonies: Testimony[] = [];
  isLoading = true;

  constructor(private httpTestimony: TestimonyService) {}

  ngOnInit() {
    this.getTestimonies();
  }

  getTestimonies() {
    this.httpTestimony.getTestimonies().subscribe(
      data => {
        this.listeTestimonies = data;
        this.listeTestimonies.forEach(testimony => {
          testimony.photoLink = environment.photoBaseApi + testimony.photoLink;
        });
      },
      error => {
        console.log(error);
      },
      () => {
        this.isLoading = false;
      }
    );
  }

  onDelete(testimony: Testimony) {
    if (
      confirm(
        "tes-vous sr de vouloir supprimer le tmoignage de " +
          testimony.testimonyFName +
          " " +
          testimony.testimonyLName +
          "?"
      )
    ) {
      this.isLoading = true;
      this.httpTestimony.deleteTestimony(testimony.id).subscribe(
        data => {
        },
        error => {
          console.log(error);
          this.isLoading = false;
        },
        () => {
          this.getTestimonies();
        }
      );
    }
  }
}
